import React from 'react';
import { Container, Divider } from '@material-ui/core';
import { Link } from "react-router-dom";
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';

const TesteHipoteses = () => {

    return (
        <>
            <Link to='/subject/TestesHipoteses/Info' replace>
                <h3>
                    Teste de Hipóteses
                </h3>
            </Link>
            <Divider />

            <List>
                <Link to='/subject/TestesHipoteses/IndependenciaHomogeniedade' replace>
                    <ListItem button key={1}>
                        <ListItemText>
                            Independência e Homogeniedade
                        </ListItemText>
                    </ListItem>
                </Link>
            </List>
            <List>
                <Link to='/subject/TestesHipoteses/Aderencia' replace>
                    <ListItem button key={1}>
                        <ListItemText>
                            Aderência
                        </ListItemText>
                    </ListItem>
                </Link>
            </List>
            <List>
                <Link to='/subject/TestesHipoteses/ExatoFisher' replace>
                    <ListItem button key={1}>
                        <ListItemText>
                            Exato de Fisher
                        </ListItemText>
                    </ListItem>
                </Link>
            </List>
        </>
    );
}

export default TesteHipoteses;